<template>
  <div class="Title" :class="seaFixed == true ? 'act' : ''">
    <div class="mucBox" @click="play">
      <img src="../assets/loging.jpg" />
      <audio src="../assets/bgmuc.mp3" id="audio"></audio>
    </div>
    <div class="menuBox">
      <tr class="trBox" v-show="Titleflag">
        <td v-for="(item,index) in lists" :key="index" @mouseenter="selB(index)">
          <a>{{item}}</a>
        </td>
        <div class="sel" id = 'selId' @click="selC"></div>
      </tr>
      <div class="minBox" v-show="!Titleflag">
        <div class="imgBox">
          <img src="../assets/caidan.png" @click="expand">
        </div>
        <div class="contentBox" v-show="cflag">
          <p v-for="(item,index) in lists" :key="index" @click="selD(index)">{{item}}</p>
          <!-- <p>关于我</p>
          <p>前端技能</p>
          <p>作品展示</p>
          <p>我的经历</p>
          <p>联系我</p>
          <p>留言板</p> -->
        </div>
      </div>
    </div>
  </div>
</template>



<style lang="scss" scoped>
.act {
  position: fixed;
  width: 100%;
  top: 0;
}
.Title {
  height: 3.75rem;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  color: #fff;
  background: #345;
  z-index: 10;
  justify-content: space-between;
  .mucBox {
    width: 3.125rem;
    height: 3.125rem;
    perspective: 1000px;
    transform-style: preserve-3d;
    position: absolute;
    &:hover {
      img {
        box-shadow: 0 0 20px #765b3e, 0 0 5px #3f4245;
      }
    }
    img {
      margin-left: .9375rem;
      width: 3.125rem;
      height: 3.125rem;
      border-radius: 50%;
      box-shadow: 0 0 20px #409eff, 0 0 5px #3f4245;
      transform: translateY(5px);
    }
  }
  .menuBox {
    line-height: 3.75rem;
    box-sizing: border-box;
    width: 100%;
    .trBox {
      position: absolute;
      right: 0;
      td {
        padding: 0 0.9375rem;
        a {
          display: block;
          width: 4.375rem;
          text-align: center;
          text-decoration: none;
          color: #fff;
        }
      }
      .sel {
        display: inline-block;
        position: absolute;
        top: 10px;
        height: 2.5rem;
        width: 6.25rem;
        border-radius: 20px;
        background: rgba(43, 58, 73, 0.2);
        box-shadow: inset 1px 1px 2px #1a1a1a;
        transition: all 0.3s ease;
        right: 0;
      }
    }
  }
}

.Title{
  .menuBox{
    .minBox{
      .imgBox{
        text-align: center;
        img{
          margin: auto;
          width: 3.125rem;
          height: 3.125rem;
          margin: 0;
          padding: 0;
       }
      }
      .contentBox{
        p{
          margin: 0;
          background: rgba(51,68,85,.5);
          text-align: center;
          padding: .3125rem;
          border-bottom: 1px solid #50739B;
          font-size: 1rem;
          font-weight: 400;
          &:nth-child(1){
            margin-top: -0.875rem;
          }
        }
      }
    }
  }
}


</style>


<script>
export default {
  name: "Title",
  data() {
    return {
      //屏幕宽度
      fullWidth:document.documentElement.clientWidth,
      fullH:document.documentElement.clientHeight,
      Titleflag: true,
      seaFixed: false,
      cflag:false,
      // offsetTop: 0,
      lists: [
        "关于我的",
        "前端技能",
        "联系我",
        "留言板"
      ]
    };
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    window.addEventListener("scroll", this.selX);
    //获取需要的固定元素，与顶部的偏移量
    this._data.offsetTop = window.screen.height;

     const that = this
      window.onresize = () => {
        return (() => {
          window.screenWidth = document.body.clientWidth
          that.fullWidth = window.screenWidth
          if(that.fullWidth > 751){
            return that.Titleflag = true;
          }else{
            return that.Titleflag = false;
          }
        })()
    }
  },
  destroyed() {
    //监听滚动事件
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    play() {
      if (audio.paused) {
        audio.play();
      } else {
        audio.pause();
      }
    },
    selB(index) {
      let _width = index * 6.25;
      let El = document.getElementsByClassName("sel")[0];
      El.style.left = _width + "rem";
      this.selID = index;
    },
    selC() {
      let a = this.selID + 1;
      const bodyH = this.fullH;
      document.documentElement.scrollTop = bodyH * a;
    },
    selD(index){
      let a = index + 1;
      const bodyH = this.fullH;
      document.documentElement.scrollTop = bodyH * a;
    },
    //滚动方法
    handleScroll() {
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > this.fullH) {
        this.seaFixed = true;
      } else {
        this.seaFixed = false;
      }
    },
    expand() {
      this.cflag = !this.cflag;
    },
    selX() {
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      let a = parseInt(scrollTop / this.fullH);
      if (a < 4) {
        let H = a * 6.25 + "rem";
        document.getElementsByClassName("sel")[0].style.left = H;
        return;
      } else {
        return;
      }
    }
  }
};
</script>