<template>
  <div class="page">
    <div v-for="(item,index) in listA" :key="index" class="itemA">{{item.textA}}</div>
    <div v-for="(item,index) in listB" :key="index" class="itemB">{{item.textA}}</div>
    <div v-for="(item,index) in listC" :key="index" class="itemC">{{item.textA}}</div>
    <div v-for="(item,index) in listD" :key="index" class="itemD">{{item.textA}}</div>
    <div class="textBox">
      <input id="1" type="text" />
      <a :id="idA" @click="add(idA)">发送</a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      idA: 0,
      listA: [{ id: "0", textA: "我是你爸爸" }],
      listB: [{ id: "0", textA: "体育场老地方见" }],
      listC: [{ id: "0", textA: "我是弹幕呀~~~~~" }],
      listD: [{ id: "0", textA: "主播好帅呀 ，i了i了~~" }]
    };
  },
  methods: {
    add(k) {
      this.idA++;
      let that = this;
      let textA = document.getElementById("1").value;
      console.log(textA);
      if (parseInt(k / 2) == k / 2) {
        if (parseInt(k / 4) == k / 4) {
          let id = that.listA.length;
          let textA = document.getElementById("1").value;
          that.listD.push({ id: id, textA: textA });
        } else {
          let id = that.listA.length;
          let textA = document.getElementById("1").value;
          that.listA.push({ id: id, textA: textA });
        }
      }
      if (parseInt(k / 3) == k / 3) {
        let id = that.listA.length;
        let textA = document.getElementById("1").value;
        that.listC.push({ id: id, textA: textA });
      } else {
        let id = that.listA.length;
        let textA = document.getElementById("1").value;
        that.listB.push({ id: id, textA: textA });
      }
    }
  }
};
</script>

