import Vue from "vue";
import Router from "vue-router";
import Interview from "./views/Interview.vue";
import Home from "./views/Home.vue";

Vue.use(Router);

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/a",
      name: "Interview",
      component: Interview
    },   
  ]
});

export default router;