<template>
  <div class="Message">
    <div class="title">
      <h1>留言板</h1>
      <h1>-Message board-</h1>
    </div>
    <div class="contentBox">
      <ul>
        <li class="inpA">
          <textarea
            class="textA"
            placeholder="写下你想说的话叭~~~"
            style="overflow: hidden; overflow-wrap: break-word; resize: none; height: 72px;"
          ></textarea>
          <a @click="SendA">发送</a>
        </li>
        <li v-for="item in Comments" :key="item.id">
          <p>用户名：<span class="nameA">{{item.name}}</span></p>
          <p>{{item.textC}}</p>
        </li>
      </ul>
      
    </div>
  </div>
</template>



<style lang="scss" scoped>
* {
  list-style-type: none;
}
.Message {
  width: 100%;
  height: 100vh;
  background: url(../assets/message_bg3.jpg) repeat fixed top;
  box-sizing: border-box;
  overflow: auto;
  .title {
    text-align: center;
    padding-top: 100px;
  }
  .contentBox {
    display: flex;
    justify-content: center;
    ul {
      width: 100%;
      li {
        background-color: #ffffff;
        width: 90%;
        border-radius: 0.625rem;
        padding: .3125rem 1.25rem;
        box-sizing: border-box;
        margin-top: 1.25rem;
        overflow: hidden;
        p{
          .nameA{
            color: coral;
          }
        }
        .textA {
          background-color: #ffffff;
          overflow: hidden;
          overflow-wrap: break-word;
          resize: none;
          width: 100%;
          border-radius: 0.625rem;
          border:none;
          box-sizing: border-box;
          padding: .3125rem 1.25rem;
          font-size: 1.25rem;
          outline: none;
        }
      }
      .inpA{
        position: relative;
        a{
          position: absolute;
          bottom: .3125rem;
          right: .625rem;
          display: inline-block;
          text-align: center;
          line-height: 1.875rem;
          padding: 0 1.25rem;
          height: 1.875rem;
          border-radius: .3125rem;
          background-color: #6190E8;
        }
      } 
    }
  }
}

.Message::-webkit-scrollbar{
  width: 0;
}
</style>



<script>
export default {
  name: "Message",
  data() {
    return {
      Comments: [
        { id: "0", name: "某公司大佬~~~", textC: "小伙子不错啊，我很欣赏。" },
        {
          id: "1",
          name: "某小白(●'◡'●)",
          textC: "哇,看了这个我也想做一个自己的网站了"
        }
      ]
    };
  },
  methods: {
    SendA: function() {
      let id = this.Comments.length
      let name = "游客12138"
      let val = document.documentElement.getElementsByClassName('textA')[0].value
      this.Comments.push({
        id:id,
        name:name,
        textC:val
      })
      document.documentElement.getElementsByClassName('textA')[0].value = '';
    }
  }
};
</script>